<template>
  <div
    class="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 space-y-8 text-center"
  >
    <img
      src="@/assets/end-trial-warning.svg"
      alt="end-trial-warning"
      class="w-96 mx-auto mb-4"
    />
    <div class="text-4xl text-grey-darken">
      Your 14-day free trial has ended
    </div>
    <div class="text-md">
      Access a complete buyback platform with simple, pay-as-you-go pricing
    </div>
    <div class="text-center">
      <Button
        @click="upgradeCheckout"
        :loading="loading.checkout"
        view="outlined"
        color="teal"
        >Subscribe</Button
      >
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useBuybackPlan } from 'composable/setting'
import { useUser } from 'composable/user'

import Button from 'bbUi/components/button/button.vue'

import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'EndTrial',
  components: {
    Button
  },
  async setup() {
    const { actionCheckout, stateCheckout, loading } = useBuybackPlan()
    const { isTrialEnd } = useUser()
    const route = useRoute()
    const router = useRouter()

    if (!isTrialEnd.value)
      return router.push({
        name: 'dashboard'
      })

    const upgradeCheckout = async () => {
      await actionCheckout({})
      if (stateCheckout.value.url)
        window.location.replace(stateCheckout.value.url)
      else
        ElMessage({
          type: 'error',
          message: `There's an error`
        })
    }

    return {
      route,
      upgradeCheckout,
      stateCheckout,
      loading
    }
  }
})
</script>
